import React, { useState } from "react";
import { Nav } from "react-bootstrap";
import "./EventCategoryNav.css";
import {
  CODING_EVENT,
  WEBINAR,
  BOOTCAMP_EVENT,
  WORKSHOP,
  ALL_EVENTS,
  categoryList
} from "../../constants/eventConstants";

export default function EventCategoryNav() {
  const [selectedCategory, setSelectedCategory] = useState(ALL_EVENTS);
  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
  };

  return (
    <div>
      <div className="events-bar">
        <Nav
          className="nav-elements-container flex-nowrap"
          defaultActiveKey="/home"
        >
          {categoryList.map((category, key) => {
            return (
              <Nav.Item
                key={key}
                onClick={() => handleCategorySelect(category.name)}
              >
                <Nav.Link
                  className={`nav-elements-c ${
                    selectedCategory === category.name &&
                    "event-category-selected"
                  }`}
                >
                  <img
                    alt="something"
                    className="nav-elements-img"
                    src={
                      selectedCategory === ALL_EVENTS
                        ? category.selectedImage
                        : category.unselectedImage
                    }
                  />
                  All Events
                </Nav.Link>
              </Nav.Item>
            );
          })}

          <Nav.Item onClick={() => handleCategorySelect(ALL_EVENTS)}>
            <Nav.Link
              className={`nav-elements-c ${
                selectedCategory === ALL_EVENTS && "event-category-selected"
              }`}
            >
              <img
                alt="something"
                className="nav-elements-img"
                src={`https://www.codingninjas.com/assets-landing/images/all-events-${
                  selectedCategory === ALL_EVENTS ? "selected" : "unselected"
                }.svg`}
              />
              All Events
            </Nav.Link>
          </Nav.Item>

          <Nav.Item onClick={() => handleCategorySelect(WEBINAR)}>
            <Nav.Link
              className={`nav-elements-c ${
                selectedCategory === WEBINAR && "event-category-selected"
              }`}
              eventKey="link-1"
            >
              <img
                alt="something"
                className="nav-elements-img"
                src={`https://www.codingninjas.com/assets-landing/images/webinar-${
                  selectedCategory === WEBINAR ? "selected" : "unselected"
                }.svg`}
              />
              Webniar
            </Nav.Link>
          </Nav.Item>

          <Nav.Item onClick={() => handleCategorySelect(CODING_EVENT)}>
            <Nav.Link
              className={`nav-elements-c ${
                selectedCategory === CODING_EVENT && "event-category-selected"
              }`}
              eventKey="link-2"
            >
              <img
                alt="something"
                className="nav-elements-img"
                src={`https://www.codingninjas.com/assets-landing/images/coding-events-${
                  selectedCategory === CODING_EVENT ? "selected" : "unselected"
                }.svg`}
              />
              Coding Events
            </Nav.Link>
          </Nav.Item>

          <Nav.Item onClick={() => handleCategorySelect(BOOTCAMP_EVENT)}>
            <Nav.Link
              className={`nav-elements-c ${
                selectedCategory === BOOTCAMP_EVENT && "event-category-selected"
              }`}
              eventKey="link-2"
            >
              <img
                alt="something"
                className="nav-elements-img"
                src={`https://files.codingninjas.in/bootcamp_events_${
                  selectedCategory === ALL_EVENTS ? "selected" : "unselected"
                }-5397.png`}
              />
              Bootcamp Events
            </Nav.Link>
          </Nav.Item>

          <Nav.Item onClick={() => handleCategorySelect(WORKSHOP)}>
            <Nav.Link
              className={`nav-elements-c ${
                selectedCategory === WORKSHOP && "event-category-selected"
              }`}
              eventKey="link-2"
            >
              <img
                alt="something"
                className="nav-elements-img"
                src="https://files.codingninjas.in/workshop_unselected-5395.png"
              />
              Workshop
            </Nav.Link>
          </Nav.Item>
        </Nav>
      </div>
    </div>
  );
}
