import React, { useState } from "react";
import {
  Offcanvas,
  OffcanvasHeader,
  OffcanvasTitle,
  OffcanvasBody
} from "react-bootstrap";
import "./TagsListMobile.css";
import TagsList from "../TagsList/TagsList";
export default function TagsListMobile(props) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  console.log(Offcanvas);
  return (
    <div>
      <div className="filter-tags-button-container" onClick={handleShow}>
        <img
          alt="filter events"
          className="filter-tags-button-img"
          src="https://www.codingninjas.com/assets-landing/images/filter-events-wave.svg"
        />
        <div className="filter-tags-button-text"> Filter Events </div>
      </div>

      <div className="offcanvas-container">
        <div className="offcanvas-header-c">
          <img
            src="https://www.codingninjas.com/assets-landing/images/events-back.svg"
            alt="back"
          />
          <div className="offcanvas-header-text">Filter Events</div>

          <div className="offcanvas-body-c">
            {props.tags.map((tag, key) => {
              return (
                <div key={key} className="tag-mobile">
                  {tag}
                </div>
              );
            })}
          </div>

          <div className="offcanvas-apply-button">Apply Filters</div>
        </div>
      </div>
    </div>
  );
}
